// COMMON :: START


// wrapper
.wrapper {
  z-index: 98;
  overflow: hidden;
  position: fixed;
  top: 108px;
  left: 108px;
  width: 100%;
  height: calc(100vh - 108px);
  @include respond($mq-max-large) {
    overflow-x: auto;
    display: inline-block;
  }
  @include respond($mq-max-small) {
    top: 68px;
    left: 75px;
    height: calc(100vh - 68px);
  }

  &__cover {
    display: flex;
    width: 100%;
    height: 100%;
    @include respond($mq-max-large) {
      width: calc(593px + 499px + 108px);
    }
    @include respond($mq-max-small) {
      width: calc(593px + 499px + 75px);
    }
  }
}
// wrapper :: end


// main
.main {
  overflow-y: auto;
  width: calc(100% - (499px + 108px));
  height: calc(100vh - 108px);
  min-height: calc(100vh - 108px);
  max-height: calc(100vh - 108px);
  @include transition(padding-left);
  @include respond($mq-max-large) {
    width: 593px;
  }
  @include respond($mq-max-small) {
    height: calc(100vh - 68px);
    min-height: calc(100vh - 68px);
    max-height: calc(100vh - 68px);
  }

  .is-menu-open & {
    @include respond($mq-wide-large) {
      padding-left: 257px;
      @include transition(padding-left);
    }
  }
}
// main :: end


// section
.section {
  overflow-y: auto;
  width: 499px;
  height: calc(100vh - 108px);
  min-height: calc(100vh - 108px);
  max-height: calc(100vh - 108px);
  border-left: 1px solid #ECECEE;
  @include respond($mq-max-large) {
    width: 499px;
  }
  @include respond($mq-max-small) {
    height: calc(100vh - 68px);
    min-height: calc(100vh - 68px);
    max-height: calc(100vh - 68px);
  }

  &__wrapper {
    position: relative;
    height: 100%;
  }

  &__title {
    display: flex;
    align-items: center;
    height: 91px;
    padding-left: 30px;
    padding-right: 30px;
    border-bottom: 1px solid #ECECEE;

    &--checkbox {
      display: flex;
      align-items: center;

      .c-form__field {
        margin-right: 30px;
        margin-bottom: 0;
      }
    }

    p {
      color: #25265E;
      font-size: 18px;
      font-weight: bold;
    }
  }

  &__scroll {
    overflow-y: auto;
    position: relative;
    min-height: 500px;
    height: calc(100vh - (108px + 91px));
    padding-bottom: 150px;
    @include respond($mq-max-large) {
      padding-bottom: 120px;
    }

    #section & {
      height: calc(100% - (169px + 91px));
    }

    &--noTransactions {
      @include fluid(padding-top, 320px, 1920px, 50px, 101px);
      @include fluid(padding-bottom, 320px, 1920px, 50px, 101px);
    }

    &--cvv {
      display: flex;
      align-items: center;
      justify-content: center;
    }

    &--historyEmpty {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }

  &__btn {
    z-index: 99;
    overflow: hidden;
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    right: calc(40px + 499px);
    bottom: 40px;
    width: 72px;
    height: 72px;
    border-radius: 100%;
    background-color: #007BFF;
    box-shadow: 0 4px 29px 0 rgba(#7540EE, 0.4);
    @include transition(box-shadow);
    @include respond($mq-max-large) {
      right: 30px;
      bottom: 30px;
    }

    &:hover {
      box-shadow: 0 2px 29px 0 rgba(#7540EE, 0.2);
      @include transition(box-shadow);

      svg {
        transform: scale(1.2);
        @include transition(transform);
      }
    }

    .icon-svg {
      fill: $c-white;
      @include transition(transform);
    }
  }

  &__info {
    display: block;
    height: 169px;
    padding-top: 40px;
    padding-left: 40px;
    background-color: #2DC76D;
    background-image: url('../img/img-circle.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    &-wrapper {
      display: flex;

      > div {
        &:first-child {
          width: 48px;

          svg {
            fill: rgba($c-white, 0.5);
          }
        }

        &:last-child {
          width: calc(100% - 48px);
        }
      }
    }

    &-title {
      display: flex;
      align-items: center;
      padding-bottom: 10px;
      color: $c-white;
      line-height: 1;
      letter-spacing: 0.35px;
      font-size: 24px;
      font-weight: bold;

      span {
        margin-right: 10px;
      }

      .icon-svg {
        width: 30px;
        height: 15px;
        fill: rgba($c-white, 0.5);
      }
    }

    &-description {
      color: $c-white;
      letter-spacing: 0.26px;
      font-size: 18px;
      font-weight: bold;
    }
  }
}
// section :: end


// COMMON :: END